.workshop {
  background-image: url('../img/wsh-back-desktop@1x.jpg');
  background-size: cover;
  background-position: 50% 50%;
  padding-top: 123px;
  padding-bottom: 96px;
}

.workshop__content {
  width: 75%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.workshop__img {
  width: 45%;
  margin-right: 59px;

    img {
      max-width: 100%;
    }
}

.workshop__text {
  width: 49%;

    h2 {
      font-family: "Hagin Caps Medium";
      font-style: normal;
      font-weight: 500;
      font-size: 48px;
      line-height: 56px;
      text-align: left;
      text-transform: uppercase;
      color: #baa480;
      margin: 0 0 10px 0;
    }

    p {
      font-family: "Roboto";
      font-style: normal;
      font-weight: normal;
      font-size: 22px;
      line-height: 30px;
      letter-spacing: 0.2px;
      color: #e9e1d5;
      margin: 0;
    }

    .workshop__first-paragraph {
      margin-bottom: 19px;
    }

    .workshop__second-paragraph {
      margin-bottom: 47px;
    }

    a {
      display: inline-block;
      font-family: "Hagin Caps Medium";
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 30px;
      text-align: center;
      text-transform: uppercase;
      text-decoration: none;
      color: #baa480;
      border: 1px solid #baa480;
      width: 47%;
      padding-top: 15px;
      padding-bottom: 15px;
    }
}
